    //#region Event server api (save, delete, test, sync...)
    private async dataSave(): Promise<void> {
        let param: { datas: Model.IDataSaveParam[] } = { datas: [] };
        for (let content of this.enrollItem.fileContent) {
            let tempParamData: Model.IDataSaveParam = {
                objectId: undefined,
                name: content.name.dataText,
                tagIds: content.tagIds.selectionIdArray,
                expiredDate: new Date(content.endDate.dataText),
            };

            tempParamData.images = [];
            if (content.photo0.dataText) {
                tempParamData.images.push({ mode: 'create', imageBase64: content.photo0.dataText, imageOrientation: content.photo0.orientation });
            }

            if (content.photo1.dataText) {
                tempParamData.images.push({ mode: 'create', imageBase64: content.photo1.dataText, imageOrientation: content.photo1.orientation });
            }

            if (content.photo2.dataText) {
                tempParamData.images.push({ mode: 'create', imageBase64: content.photo2.dataText, imageOrientation: content.photo2.orientation });
            }

            if (content.photo3.dataText) {
                tempParamData.images.push({ mode: 'create', imageBase64: content.photo3.dataText, imageOrientation: content.photo3.orientation });
            }

            if (content.photo4.dataText) {
                tempParamData.images.push({ mode: 'create', imageBase64: content.photo4.dataText, imageOrientation: content.photo4.orientation });
            }

            if (content.employeeId.dataText) tempParamData.employeeId = content.employeeId.dataText;

            if (content.position.dataText) tempParamData.position = content.position.dataText;

            if (content.phone.dataText) tempParamData.contactNumber = content.phone.dataText;

            if (content.email.dataText) tempParamData.email = content.email.dataText;

            if (content.remark.dataText) tempParamData.remark = content.remark.dataText;

            if (content.card.dataText) tempParamData.card = content.card.dataText.toString();

            param.datas.push(tempParamData);
        }

        this.progressItem.max = param.datas.length;

        let response: { datas: IComponent.IStatusCodeObjectIdMessageResponse[] } = { datas: [] };

        this.progressItem.showProgress = true;

        await Batch<Model.IDataSaveParam>(this.progressItem.howManyDataATime, param.datas, async (subDatas, index) => {
            await this.delay(1000);
            this.progressItem.value += this.progressItem.howManyDataATime;
            for (let i = 0; i < param.datas.length; i++) {
                response.datas.push({
                    statusCode: 200,
                    message: '',
                    objectId: '',
                });
            }
        });

        if (response.datas.length >= param.datas.length) {
            this.resolveResponse(response);
        }

        this.showProgressItemClear();
    }
