    //#region Other Function
    private async uploadExcelOnly(): Promise<void> {
        this.enrollItem.fileContent.forEach((item) => {
            let isBase64Photo0: boolean = ServiceBase64.isBase64Image(item.photo0.dataText);
            let isBase64Photo1: boolean = ServiceBase64.isBase64Image(item.photo1.dataText);
            let isBase64Photo2: boolean = ServiceBase64.isBase64Image(item.photo2.dataText);
            let isBase64Photo3: boolean = ServiceBase64.isBase64Image(item.photo3.dataText);
            let isBase64Photo4: boolean = ServiceBase64.isBase64Image(item.photo4.dataText);
            let isHaveImage: boolean = isBase64Photo0 || isBase64Photo1 || isBase64Photo2 || isBase64Photo3 || isBase64Photo4;
            let isHaveCard: boolean = !!item.card.dataText;

            item.card.errorMessage = '';
            item.photo0.errorMessage = '';

            if (!isHaveCard && !isHaveImage) {
                item.card.errorMessage = this.$i18n.Person_Card_Photo_Tip;
                item.photo0.errorMessage = this.$i18n.EnrollErrorImageNotFind;
            }
        });

        this.enrollItem.fileError = this.enrollItem.fileContent.some((item) => {
            for (let key in item) {
                if (!!item[key].errorMessage) {
                    return true;
                }
            }
        });
    }

    private async resolveImageToBase64(): Promise<void> {
        try {
            this.loading.show();
            for (let titleIndex in this.enrollItem.title) {
                let title = this.enrollItem.title[titleIndex];
                let titleCustom = title.custom;
                if (titleCustom.type !== Model.EContentType.image) continue;

                for (let i in this.enrollItem.fileContent) {
                    let content = this.enrollItem.fileContent[i];
                    let haveImage = false;

                    if (!content[title.key] || !content[title.key].dataText) {
                        continue;
                    }

                    for (let file of this.enrollItem.fileImageArray) {
                        if (file.name.toLowerCase() === content[title.key].dataText.toLowerCase()) {
                            haveImage = true;

                            let base64 = await ServiceBase64.getBase64(file);

                            if (base64.result) {
                                this.enrollItem.fileContent[i][title.key].dataText = base64.result;
                                this.enrollItem.title[titleIndex].custom.base64Length++;

                                let orientation: number = (await this.imageGetExif(file)) || 1;
                                this.enrollItem.fileContent[i][title.key].orientation = orientation;
                                this.imageCheckHave();
                            } else {
                                this.enrollItem.fileContent[i][title.key].errorMessage = this.$i18n.EnrollErrorImageNotFind;
                                this.enrollItem.title[titleIndex].custom.base64Length++;
                                this.imageCheckHave();
                            }
                        }
                    }
                    if (!haveImage) {
                        this.enrollItem.fileContent[i][title.key].errorMessage = this.$i18n.EnrollErrorImageNotFind;
                        this.enrollItem.title[titleIndex].custom.base64Length++;
                        this.imageCheckHave();
                    }
                }
            }

            this.uploadExcelOnly();
        } catch (error) {
        } finally {
            this.loading.hide();
        }
    }
