    //#region Event download
    private downloadExampleCSV(): void {
        let titleText: string = '';
        let contentText: string = '';
        let fileContent: string = '';
        let contents: Model.IExampleContent[] = this.initExampleContent();
        for (let title of this.enrollItem.title) {
            let tempTitle: string = title.header as string;
            tempTitle = tempTitle.replace(',', '');
            tempTitle = tempTitle.trim();
            titleText += `${tempTitle},`;
        }
        titleText = titleText.substr(0, titleText.length - 1);
        titleText += '\n';
        for (let content of contents) {
            for (let title of this.enrollItem.title) {
                let tempKey: string = title.key as string;
                contentText += `${content[tempKey]},`;
            }
            contentText = contentText.substr(0, contentText.length - 1);
            contentText += `\n`;
        }
        fileContent = titleText + contentText;
        fileContent = fileContent.substr(0, fileContent.length - 1);
        this.saveCSV(fileContent, this.enrollItem.sampleFileName);
    }

    private downloadExampleExcel(): void {
        let workbook: Excel.Workbook = new Excel.Workbook();
        let worksheet: Excel.Worksheet = workbook.addWorksheet(this.enrollItem.sheetName);
        let contents: Model.IExampleContent[] = this.initExampleContent();
        worksheet.columns = this.enrollItem.title;
        for (let content of contents) {
            worksheet.addRow(content);
            worksheet.lastRow.height = this.excelStyle.rowHeight;
        }
        workbook = this.downloadSelectionSheet(workbook);
        this.saveExcel(workbook, this.enrollItem.sampleFileName);
    }
