    //#region Event upload file
    private async uploadDirectory(files: File[]): Promise<boolean> {
        if (!files || files.length < 1) return false;

        this.enrollItem.isChooseDirectory = true;

        this.handleImageDirectory();
    }

    private uploadFile(file: File): boolean {
        if (!file) return false;

        this.enrollItem.isChooseFile = true;
        this.enrollItem.fileContent = [];

        // 
        let extension: string = file.name.split('.')[1];

        if (!extension) {
            ServiceDialog.ErrorConfirmOnly(this, this.$i18n.EnrollErrorFileTypeExcel, this.$i18n.Common_Status_Error);
            return false;
        }

        extension = extension.toLowerCase();

        switch (extension) {
            case IViews.EFileType.xlsx:
                this.resolveExcel(file);
                break;
            case IViews.EFileType.csv:
                let reader = new FileReader();
                reader.onload = (file) => {
                    if (file && file.target && file.target.result) {
                        this.resolveCSV(file.target.result as string);
                    } else {
                        ServiceDialog.ErrorConfirmOnly(this, this.$i18n.EnrollErrorFileTypeCSV, this.$i18n.Common_Status_Error);
                        return false;
                    }
                };
                reader.readAsText(file);
                break;
            default:
                ServiceDialog.ErrorConfirmOnly(this, this.$i18n.EnrollErrorFileTypeExcel, this.$i18n.Common_Status_Error);
                return false;
        }
    }
