    //#endregion  View Event

    //#region Other Function
    //#region Save
    private saveExcel(workbook: Excel.Workbook, fileName: string): void {
        workbook.xlsx.writeBuffer().then((data) => {
            let blob = new Blob([data], {
                type: this.excelStyle.excelFileType,
            });
            saveAs(blob, `${fileName}.xlsx`);
        });
    }

    private saveCSV(content: string, fileName: string): void {
        let blob = new Blob([content], {
            type: this.enrollItem.csvFileType,
        });
        saveAs(blob, `${fileName}.csv`);
    }
    //#endregion

    //#region download selection
    private downloadSelectionSheet(workbook: Excel.Workbook): Excel.Workbook {
        let selection: Model.ISelectionItem = this.selectOptionItem;
        let excelWidth: number = this.excelStyle.columnWidth;
        let excelRowHeight: number = this.excelStyle.rowHeight;
        Object.keys(selection).map(function (objectKey, index) {
            let value: Model.ISelectionItemData = selection[objectKey];
            let worksheet: Excel.Worksheet = workbook.addWorksheet(value.name);
            worksheet.columns = [
                {
                    header: value.name,
                    key: 'name',
                    width: excelWidth,
                },
            ];
            for (let tempValue of value.datas) {
                worksheet.addRow({
                    name: tempValue.name,
                });
                worksheet.lastRow.height = excelRowHeight;
            }
        });
        return workbook;
    }
    //#endregion

    //#region handle image directory
    private handleImageDirectory(): void {
        if (this.enrollItem.isChooseFile && this.enrollItem.isChooseDirectory) {
            this.resolveImageToBase64();
        }
    }
    //#endregion

    //#region Resolve upload file
    private resolveExcel(file: File): void {
        let workbook = new Excel.Workbook();
        let reader = new FileReader();
        reader.readAsArrayBuffer(file);
        reader.onload = () => {
            const buffer = reader.result as ArrayBuffer;
            workbook.xlsx.load(buffer).then((workbook) => {
                if (workbook.worksheets && workbook.worksheets[0]) {
                    workbook.worksheets[0].eachRow((row, rowIndex) => {
                        if (rowIndex === 1) return false;

                        let tempValues = [];
                        for (let i in row.values) {
                            let tempRowValue: any = row.values[i];
                            let tempValue: string = '';

                            // check row.values[i] type
                            if (ServiceTypeof.isString(tempRowValue) || ServiceTypeof.isNumber(tempRowValue)) {
                                tempValues[parseInt(i) - 1] = tempRowValue;
                            } else if (ServiceTypeof.isObject(tempRowValue)) {
                                if (tempRowValue && tempRowValue.richText) {
                                    // 
                                    (tempRowValue.richText || []).forEach((element) => {
                                        if (element.text) tempValue += element.text;
                                        tempValues[parseInt(i) - 1] = tempValue;
                                    });
                                } else if (tempRowValue && tempRowValue.text && tempRowValue.hyperlink) {
                                    // email
                                    tempValues[parseInt(i) - 1] = tempRowValue.text;
                                } else {
                                    console.error('error => ', this.$i18n.EnrollErrorDeveloperSetting);
                                }
                            } else if (ServiceTypeof.isDate(tempRowValue)) {
                                // 
                                tempValues[parseInt(i) - 1] = ServiceDatetime.datetime2String(tempRowValue, 'YYYY/MM/DD');
                            } else {
                                console.error('error => ', this.$i18n.EnrollErrorDeveloperSetting);
                            }
                        }
                        this.resolveFileContent(tempValues);
                        this.handleImageDirectory();
                    });
                }
                workbook;
            });
        };
    }

    private resolveCSV(fileResult: string): void {
        let fileRows: string[] = fileResult.split('\n');
        for (let i in fileRows) {
            if (parseInt(i) === 0) continue;

            let fileRow: string = fileRows[i];
            let fileColumn: string[] = fileRow.split(',');
            this.resolveFileContent(fileColumn);
        }
        this.handleImageDirectory();
    }

    private resolveFormatFileNames(file: File[]): string | boolean {
        if (!file) return false;
        return file.length === 1 ? file[0].name : `${file.length} ${this.$i18n.Form_FilesSelected}`;
    }
    //#endregion

    //#region Image
    private async imageGetExif(file: File): Promise<number> {
        if (!!file) {
            let orientation: number = await new Promise((resolve) => {
                EXIF.getData(file as any, () => resolve(EXIF.getTag(file, 'Orientation')));
            });
            return orientation;
        }
    }

    private imageCheckType(file: File): boolean {
        if (!file) return false;

        if (!file.name) return false;

        if (!(ServiceRegex.isExtensionJpg(file.name) || ServiceRegex.isExtensionPng(file.name))) return false;

        if (!file.type) return false;

        if (file.type.indexOf('image') >= 0) return false;

        return true;
    }

    private imageCheckHave(): void {
        for (let title of this.enrollItem.title) {
            let titleCustom = title.custom;
            if (titleCustom.type != Model.EContentType.image) {
                continue;
            }
            for (let i in this.enrollItem.fileContent) {
                let content = this.enrollItem.fileContent[i];
                if (content[title.key].dataText != '') {
                    if (ServiceBase64.isBase64Image(content[title.key].dataText)) {
                        this.enrollItem.fileContent[i][title.key].errorMessage = '';
                    } else {
                        this.enrollItem.fileContent[i][title.key].errorMessage = this.$i18n.EnrollErrorImageNotFind;
                    }
                }
            }
        }
    }
    //#endregion

    //#region Resolve show table data
    private resolveFileContent(fileContent: string[]): void {
        let content: any = {};
        for (let index in this.enrollItem.title) {
            let title: Model.ITableTitle = this.enrollItem.title[index];
            let titleCustom: Model.ITableTitleCustom = title.custom;

            /**
             * 
             * errorMessage -  & 
             * original - 
             * originalText - , ,  (ex: )
             * data - 
             * dataText - ,  (ex: selection  name, date format )
             * dataTextArray - isArray  dataText
             * selectionIdArray -  API  objectId 
             */
            let fileContentDataItem: Model.IFileContentDataItem = {
                errorMessage: '',
                original: fileContent[index],
                originalText: fileContent[index] ? fileContent[index].toString().trim() : '',
                data: fileContent[index],
                dataText: fileContent[index] ? fileContent[index].toString().trim() : '',
                dataTextArray: [],
                selectionIdArray: [],
                orientation: undefined,
            };
            content[title.key] = JSON.parse(JSON.stringify(fileContentDataItem));

            // 
            if (titleCustom.require === true && !fileContentDataItem.dataText) {
                fileContentDataItem.errorMessage = this.$i18n.EnrollErrorDataRequire;
                content[title.key] = JSON.parse(JSON.stringify(fileContentDataItem));
                continue;
            }

            // 
            if (fileContentDataItem.dataText !== null && titleCustom.isArray) {
                let tempDataArray = fileContentDataItem.dataText.split(';');
                for (let temp of tempDataArray) {
                    temp = temp.trim();
                    if (temp) fileContentDataItem.dataTextArray.push(temp);
                }
            }

            // 
            switch (titleCustom.type) {
                case Model.EContentType.boolean:
                    content[title.key].data = ServiceRegex.isTrueString(content[title.key].dataText) ? true : false;
                    if (titleCustom.booleanCheck !== undefined && content[title.key].data !== titleCustom.booleanCheck) {
                        fileContentDataItem.errorMessage = titleCustom.booleanCheck
                            ? this.$i18n.EnrollErrorBooleanTrue
                            : this.$i18n.EnrollErrorBooleanFalse;
                        content[title.key] = JSON.parse(JSON.stringify(fileContentDataItem));
                        continue;
                    }
                    break;

                case Model.EContentType.int:
                    let parseDataInt = parseInt(fileContentDataItem.dataText);
                    if (titleCustom.require === true) {
                        if (!parseDataInt || isNaN(parseDataInt)) {
                            fileContentDataItem.errorMessage = this.$i18n.EnrollErrorDataNaN;
                            content[title.key] = JSON.parse(JSON.stringify(fileContentDataItem));
                            continue;
                        }
                    }

                    break;

                case Model.EContentType.number:
                    let parseDataFloat = parseFloat(fileContentDataItem.dataText);
                    if (titleCustom.require === true) {
                        if (!parseDataFloat || isNaN(parseDataFloat)) {
                            fileContentDataItem.errorMessage = this.$i18n.EnrollErrorDataNaN;
                            content[title.key] = JSON.parse(JSON.stringify(fileContentDataItem));
                            continue;
                        }
                    }
                    break;

                case Model.EContentType.date:
                    let tempDate: Date = new Date(fileContentDataItem.dataText);
                    let tempDateString: string = ServiceDatetime.datetime2String(tempDate, 'YYYY/MM/DD');
                    fileContentDataItem.dataText = JSON.parse(JSON.stringify(tempDateString));
                    if (!titleCustom.require && !fileContentDataItem.dataText) {
                        content[title.key] = JSON.parse(JSON.stringify(fileContentDataItem));
                        continue;
                    }
                    break;

                case Model.EContentType.string:
                    if (titleCustom.isArray) {
                        if (titleCustom.require && fileContentDataItem.dataTextArray.length < 1) {
                            fileContentDataItem.errorMessage = this.$i18n.EnrollErrorDataRequire;
                            content[title.key] = JSON.parse(JSON.stringify(fileContentDataItem));
                            continue;
                        }
                        for (let tempString of fileContentDataItem.dataTextArray) {
                            if (!titleCustom.stringRegex.test(tempString)) {
                                fileContentDataItem.errorMessage = this.$i18n.EnrollErrorDataRegex;
                                content[title.key] = JSON.parse(JSON.stringify(fileContentDataItem));
                                continue;
                            }
                        }
                    } else {
                        if (titleCustom.stringRegex) {
                            if (!titleCustom.require && !fileContentDataItem.dataText) {
                                content[title.key] = JSON.parse(JSON.stringify(fileContentDataItem));
                                continue;
                            }
                            if (!titleCustom.stringRegex.test(fileContentDataItem.dataText)) {
                                fileContentDataItem.errorMessage = this.$i18n.EnrollErrorDataRegex;
                                content[title.key] = JSON.parse(JSON.stringify(fileContentDataItem));
                                continue;
                            }
                        }
                    }
                    break;

                case Model.EContentType.phone:
                    let phoneRegex: RegExp = /(^\+{1}[0-9]+$)|(^[\d]+$)/;
                    if (titleCustom.isArray) {
                        if (titleCustom.require && fileContentDataItem.dataTextArray.length < 1) {
                            fileContentDataItem.errorMessage = this.$i18n.EnrollErrorDataRequire;
                            content[title.key] = JSON.parse(JSON.stringify(fileContentDataItem));
                            continue;
                        }
                        for (let tempString of fileContentDataItem.dataTextArray) {
                            if (!phoneRegex.test(tempString)) {
                                fileContentDataItem.errorMessage = this.$i18n.EnrollErrorDataPhone;
                                content[title.key] = JSON.parse(JSON.stringify(fileContentDataItem));
                                continue;
                            }
                        }
                    } else {
                        if (!titleCustom.require && !fileContentDataItem.dataText) {
                            content[title.key] = JSON.parse(JSON.stringify(fileContentDataItem));
                            continue;
                        }
                        if (!phoneRegex.test(fileContentDataItem.dataText)) {
                            fileContentDataItem.errorMessage = this.$i18n.EnrollErrorDataPhone;
                            content[title.key] = JSON.parse(JSON.stringify(fileContentDataItem));
                            continue;
                        }
                    }
                    break;

                case Model.EContentType.email:
                    if (typeof fileContentDataItem.data === 'object') {
                        fileContentDataItem.dataText = fileContentDataItem.data.text ? fileContentDataItem.data.text : '';
                        fileContentDataItem.originalText = JSON.stringify(fileContentDataItem.original);
                    }
                    if (!titleCustom.require && !fileContentDataItem.dataText) {
                        content[title.key] = JSON.parse(JSON.stringify(fileContentDataItem));
                        continue;
                    }
                    if (!ServiceRegex.isEmail(fileContentDataItem.dataText)) {
                        fileContentDataItem.errorMessage = this.$i18n.EnrollErrorDataEmail;
                        content[title.key] = JSON.parse(JSON.stringify(fileContentDataItem));
                        continue;
                    }
                    break;

                case Model.EContentType.selection:
                    if (titleCustom.isArray) {
                        let tempSelection = this.selectOptionItem[titleCustom.selectionKey].datas;
                        if (titleCustom.require && fileContentDataItem.dataTextArray.length < 1) {
                            fileContentDataItem.errorMessage = this.$i18n.EnrollErrorDataRequire;
                            content[title.key] = JSON.parse(JSON.stringify(fileContentDataItem));
                            continue;
                        }
                        fileContentDataItem.selectionIdArray = [];

                        for (let tempString of fileContentDataItem.dataTextArray) {
                            let haveSelectionValue = false;
                            for (let selectionData of tempSelection) {
                                if (selectionData.name.toLowerCase() === tempString.toLowerCase()) {
                                    haveSelectionValue = true;
                                    fileContentDataItem.selectionIdArray.push(selectionData.objectId);
                                    break;
                                }
                            }
                            if (!haveSelectionValue) {
                                fileContentDataItem.errorMessage = this.$i18n.EnrollErrorDataSelectionNotFind;
                                content[title.key] = JSON.parse(JSON.stringify(fileContentDataItem));
                                continue;
                            }
                        }
                    } else {
                        let tempSelection = this.selectOptionItem[titleCustom.selectionKey].datas;
                        let haveSelectionValue = false;
                        if (titleCustom.require === false && !fileContentDataItem.dataText) {
                            content[title.key] = JSON.parse(JSON.stringify(fileContentDataItem));
                            continue;
                        }
                        for (let selectionData of tempSelection) {
                            if (selectionData.name.toLowerCase() === fileContentDataItem.dataText.toLowerCase()) {
                                haveSelectionValue = true;
                                fileContentDataItem.selectionIdArray = [selectionData.objectId];
                                break;
                            }
                        }
                        if (!haveSelectionValue) {
                            fileContentDataItem.errorMessage = this.$i18n.EnrollErrorDeveloperSetting;
                            content[title.key] = JSON.parse(JSON.stringify(fileContentDataItem));
                        }
                    }
                    break;

                case Model.EContentType.image:
                //  resolveImageToBase64 
                default:
                    break;
            }

            // , 
            content[title.key] = JSON.parse(JSON.stringify(fileContentDataItem));
            continue;
        }

        if (this.enrollItem.maxEnrollCount > 0) {
            if (this.enrollItem.fileContent.length < this.enrollItem.maxEnrollCount) {
                this.enrollItem.fileContent.push(content);
            }
        } else {
            this.enrollItem.fileContent.push(content);
        }
    }

    private resolveTableData(title: Model.ITableTitle, content: Model.IFileContentDataItem): string {
        let result: string = '';

        if (!content[title.key]) return '';

        if (content[title.key].errorMessage) return content[title.key].originalText;

        if (title.custom.isArray) {
            result += '<ul>';
            for (let dataList of content[title.key].dataTextArray) {
                result += `<li>${dataList}</li>`;
            }
            result += '</ul>';
        } else {
            switch (title.custom.type) {
                case Model.EContentType.image:
                    if (content[title.key].dataText && content[title.key].dataText && ServiceBase64.isBase64Image(content[title.key].dataText)) {
                        result = `<img src='${content[title.key].dataText}' style='${this.enrollItem.imageStyle}'>`;
                    }
                    break;
                case Model.EContentType.date:
                case Model.EContentType.selection:
                case Model.EContentType.boolean:
                case Model.EContentType.int:
                case Model.EContentType.number:
                case Model.EContentType.string:
                case Model.EContentType.email:
                case Model.EContentType.phone:
                case Model.EContentType.none:
                default:
                    result = content[title.key].dataText;
                    break;
            }
        }

        return result;
    }
    //#endregion

    //#region Resolve server response
    private resolveResponse(response: { datas: IComponent.IStatusCodeObjectIdMessageResponse[] }): void {
        if (response.datas) {
            for (let i in response.datas) {
                let data = response.datas[i];
                if (this.enrollItem.fileContent[i] !== undefined) {
                    this.enrollItem.fileContent[i].api = {
                        statusCode: data.statusCode ? data.statusCode : '',
                        objectId: data.objectId ? data.objectId : '',
                        message: data.message ? data.message : '',
                    };
                    if (data.statusCode == 200) {
                        this.enrollItem.successCount++;
                    } else {
                        this.enrollItem.failCount++;
                    }
                }
            }
            this.enrollItem.showResult = true;
        } else {
            ServiceDialog.ErrorConfirmOnly(this, this.$i18n.Common_Status_Error, this.$i18n.Error_ResponseUndefined);
        }
    }

    private resoleveAPIStatus(data: Model.IEnrollItemFileContent): boolean {
        if (data.api && data.api.statusCode) {
            return data.api && data.api.statusCode && data.api.statusCode == 200;
        } else {
            return null;
        }
    }

    private resolveAPIMessage(data: Model.IEnrollItemFileContent = undefined): string {
        if (!data.api) {
            return '';
        } else {
            if (data.api.message && !data.api.message) {
                return data.api.message;
            } else if (data.api.statusCode && data.api.statusCode === 200) {
                return this.$i18n.EnrollEnrollSuccess;
            } else {
                return `${this.$i18n.EnrollEnrollFailed} ${data.api.message}`;
            }
        }
    }
    //#endregion

    private showProgressItemClear(): void {
        this.progressItem = JSON.parse(JSON.stringify({ ...this.progressItemOriginal }));
    }

    private async delay(ms: number): Promise<void> {
        await new Promise<void>((resolve) => {
            setTimeout(() => {
                return resolve();
            }, ms);
        });
    }

    //#endregion Other Function
    //#endregion No need to code
}

//#region Batch No need to code
/**
 * Batch
 * @param bufferCount
 * @param datas
 * @param Do
 */
export async function Batch<T>(bufferCount: number, datas: T[], Do: (subDatas: T[]) => Promise<void>): Promise<void>;
export async function Batch<T>(bufferCount: number, datas: T[], Do: (subDatas: T[], index: number) => Promise<void>): Promise<void>;
export async function Batch<T>(bufferCount: number, datas: T[], Do: (subDatas: T[], index?: number) => Promise<void>): Promise<void> {
    try {
        for (let i: number = 0; i < datas.length; i += bufferCount) {
            await Do(datas.slice(i, i + bufferCount), i);
        }
    } catch (e) {
        throw e;
    }
}
