import * as Excel from 'exceljs';

import { IComponent } from '@/../models';

export namespace BatchEnroll {
    /**
     * Index
     */
    export interface IIndexImage_Create {
        mode: 'create';
        imageBase64: string;
        imageOrientation?: number;
    }

    export interface IIndexImage_Update {
        mode: 'update';
        faceId: string;
        imageBase64: string;
        imageOrientation?: number;
    }

    export interface IIndexImage_Delete {
        mode: 'delete';
        faceId: string;
    }

    export type IIndexImageU = IIndexImage_Create | IIndexImage_Update | IIndexImage_Delete;

    export interface IIndexU {
        objectId: string;
        images?: IIndexImageU[];
        card?: string;
        name: string;
        employeeId?: string;
        position?: string;
        contactNumber?: string;
        email?: string;
        tagIds?: string[];
        remark?: string;
        additionalInfo?: object;
        password?: string;
        expiredDate: Date;
        floors?: IComponent.ISelectionBase[];
        accessGroups?: IComponent.ISelectionBase[];
    }

    export type IIndexImageC = IIndexImage_Create;

    //#region server

    export type IDataSaveParam = IIndexU;
    //#endregion

    //#region web
    export interface ISelectionItem {
        tag: ISelectionItemData;
    }

    export interface ISelectionItemData {
        name: string;
        datas: IComponent.ISelectionBase[];
    }

    export interface ITableTitle extends Partial<Excel.Column> {
        custom: ITableTitleCustom;
    }

    export interface ITableTitleCustom {
        isArray: boolean;
        require: boolean;
        type: EContentType;
        booleanCheck?: boolean;
        stringRegex?: RegExp;
        dateFormat?: string;
        selectionKey?: string;
        base64Length?: number;
    }

    export enum EContentType {
        none = 'none',
        boolean = 'boolean',
        int = 'int',
        number = 'number',
        string = 'string',
        date = 'date',
        image = 'image',
        phone = 'phone',
        email = 'email',
        selection = 'selection',
    }

    export interface IFileContentDataItem {
        errorMessage: string;
        original: any;
        originalText: string;
        data: any;
        dataText: string;
        dataTextArray: string[];
        selectionIdArray: string[];
        orientation: number;
    }

    export interface IExampleContent {
        no: number;
        photo0: string;
        photo1: string;
        photo2: string;
        photo3: string;
        photo4: string;
        employeeId: string;
        name: string;
        position: string;
        phone: string;
        email: string;
        remark: string;
        card: string;
        endDate: string;
        tagIds: string;
    }

    export interface IEnrollItemApi {
        statusCode: number | string;
        objectId: string;
        message: string;
    }

    export interface IEnrollItemFileContent {
        no?: number;
        name: IFileContentDataItem;
        tagIds: IFileContentDataItem;
        endDate: IFileContentDataItem;
        photo0: IFileContentDataItem;
        photo1: IFileContentDataItem;
        photo2: IFileContentDataItem;
        photo3: IFileContentDataItem;
        photo4: IFileContentDataItem;
        employeeId: IFileContentDataItem;
        position: IFileContentDataItem;
        phone: IFileContentDataItem;
        email: IFileContentDataItem;
        remark: IFileContentDataItem;
        card: IFileContentDataItem;
        api?: IEnrollItemApi;
        apiMessageKey?: string;
    }

    export interface IEnrollItem {
        useCSV: boolean;
        useExcel: boolean;
        useDirectory: boolean;
        isChooseFile: boolean;
        isChooseDirectory: boolean;
        title: ITableTitle[];
        sheetName: string;
        sampleFileName: string;
        sampleRowCount: number;
        resultFileName: string;
        file: File;
        fileError: boolean;
        fileContent: IEnrollItemFileContent[];
        fileImageArray: File[];
        csvFileType: string;
        imageStyle: string;
        maxEnrollCount: number;
        successCount: number;
        failCount: number;
        showResult: boolean;
    }
    //#endregion
}
